body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.instructions h1 {
  font-size: 24px;
  font-weight: 700;
}

.instructions {
  /* background-color: #00acb0; */
  background-color: white;
  padding: 5px;
  /* color: #ece7e7; */
  color: black;
}

/* form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
} */

input {
  margin: 5px;
  margin-bottom: 10px;
}

input[type="submit"] {
  margin: 10px;
}

.message {
  padding: 10px 0;
}

.message p {
  margin-left: 10px;
}

.message:nth-child(odd) {
  background-color: #eeeeee;
}

.message:nth-child(even) {
  background-color: #f8f8f8;
}

.navbar {
  /* background-color: #00acb0; */
  background-color: white;
  width: 100%;
  border-radius: 0px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/* li {
    float: left;
} */

li a {
  display: block;
  /* color: white; */
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  text-decoration: underline;
  /* color: #eee; */
  color: black;
}

.username {
  float: right;
}

.welcome {
  right: 175px;
  position: absolute;
  text-align: right;
  padding: 20px;
}

.person {
  /* right: 0px;
    position: absolute;
    padding: 10px; */
}

/* .navBar {
    width: 190px;
    align: left;
} */

.buttonGroup {
  float: right;
}

.logOut {
  float: left;
}

.leftAlign {
    float: left;
}

.rightAlign {
    float: right;
} 

/* center headings */
.centerHeadings {
  text-align: center;
}

.lengthOfInputs {
  width: 150px;
}

.lengthOfInputsCohort {
  width: 150px;
}

.wrapper {
  display: grid;
  grid-template-columns: 3fr 9fr;
}

.hSplit {
  display: grid;
  grid-template-rows: 1fr 0.2fr;
}

.item3 {
  display: grid;
  grid-template-columns: 4fr 3fr 1fr;
}

.item3b {
  display: grid;
  grid-template-rows: 1fr;
  text-align: right;
  /* padding: 0 15px 0 0; */
}

/* .item3b1 {
    display: grid;
    backround: grey;
} */

.item3b2 {
  display: grid;
  display: inline-block;
  -ms-flex-pack: right;
      justify-content: right;
}

.item3b3 {
  display: grid;
}

.item3c {
  display: grid;
  grid-template-rows: 1fr;
  /* padding: 0 0 0 15px; */
  padding: 0px 20px 0px 0px;
}
.item3c2 {
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  /* vertical-align: center; */
  -ms-flex-align: center;
      align-items: center;
}

.item3c3 {
  display: grid;
}

.item4 {
  display: grid;
  -ms-flex-pack: right;
      justify-content: right;
}

#TTTdate {
  padding: 15px 0px;
}

.linkText {
  text-decoration: none;
  text-align: right;
}

.loginContainer {
  display: grid;
  grid-template-rows: 1fr;
  -ms-flex-pack: center;
      justify-content: center;
}

.outer-div
{
     padding: 150px;
}




